}

.logo {
  display: inline-block;
  height: 80%;
  width: auto;
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  margin-left: 2%;
  opacity: 0.9;
}

.logo:hover {
  opacity: 1;
}

.wewheel {
  position: absolute;
  left: 30%;
  top: 50%;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  margin: 0;
  display: inline-block;
  color: #facd49;
  font-weight: bold;
  font-size: 4vw;
  opacity: 0.9;
}

.wewheel:hover {
  opacity: 1;
}

.navbar-nav {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: 36%;
  height: 40%;
  right: 0;
  top: 50%;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-item {
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  width: 33%;
  padding: 0;
  text-align: center;
  cursor: pointer;
  color: inherit;
}

/* Style the navigation bar links */
.nav-link {
  padding: 0;
  text-decoration: none;
  font-size: 1.5vw;
  color: inherit;
}

.nav-item:hover {
  font-weight: bold;
  transition: all 1s;
}

.active {
  font-weight: bold;
  /* text-decoration: underline; */
}
